@import url(https://fonts.googleapis.com/css2?family=Hammersmith+One&display=swap);
* {
  font-family: "Hammersmith One";
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.container {

  justify-content: center;
  align-items: center;
}
.hero-image {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(/port-folio/static/media/profileImg.b229dadc.jpg);
    height: 50vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

}

.hero-text {
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    color: white;
    font-size: 22px;
  }
.col-8, .col-4 {
    color: black;
}

#main {
    justify-content: center;
    text-align: center;
    color: black;
    font-size: 25px;
}
.col-4, #skill {
    color: black;
}
.col-12, a {
    color: white;
}
a {
    text-decoration: underline;
}
a:hover {
    color: greenyellow;
}
p {
    font-size: 20px;
}
h3 {
    font-weight: bold;
    color: white;
}
.header {
  cursor: pointer;
  color: #fff;
  font-size: 28px;
  font-weight: bold;
}

.content {
  cursor: pointer;
}
h3 {
  color: cyan;
}
#product {
  border-radius: 15px;
  -webkit-animation: fadein 3s forwards;
          animation: fadein 3s forwards;
}
#scgCollapse {
  margin-left: 100px;
}
@-webkit-keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
